import React from 'react';
import * as js  from "./contracts/MyNFT.json";
import './Contract.css';
import {useLocation} from 'react-router-dom';

function Contract() {
  const {state} = useLocation();
    
  const nftname = state;
 
 const metadata = js.default;
console.log(nftname);
  return (
    <div className="App">
      <table align="center">
        <tr>
         <td align="center"><button className="b1" onClick={check.bind(this,nftname)}>connect to wallet</button></td>
        </tr>
        <tr></tr>
        <tr>
          <td className='inf'>Please set your gas limit must be equal or greater than 5,991,856</td>
        </tr>
      </table>
    </div>
  );

 async function check(name) {

    try{
      if(window.ethereum){
        await window.ethereum.enable();
        
         }
      const Web3 = require('web3');
      const web3 = new Web3(Web3.givenProvider);
      
      const nftdata = await fetch(`https://nft-app-ec882-default-rtdb.firebaseio.com/NFT/${name}.json`,);
   
      const resdata = await nftdata.json();
     const token = await  fetch(`https://nft-app-ec882-default-rtdb.firebaseio.com/token.json`,);
    const tokendata = await token.json();
    
   const tokenId = parseInt(tokendata['count']) + 1;

     const accounts = window.ethereum.selectedAddress;

     console.log(accounts);

      const constructorArgs = [name,resdata['Nft_Symbol'],resdata['Image_url'],accounts,'0x69B4bfD57E10452658BCf483E7A4103bd5cFE92A'];

let contract = new web3.eth.Contract(metadata);

contract = await contract.deploy({
      data:'',
    arguments: constructorArgs,
  
});
      
const newContractInstance = await contract.send({
    from: accounts,
   
});
    
 if(newContractInstance.options.address != null){
   alert('your mint peocess are started');
 let cAddress = newContractInstance.options.address;
          
  const nftContract = new web3.eth.Contract(metadata,cAddress);


 

  const tx = {
    'from':accounts, 
    'to': cAddress,
    'data': nftContract.methods.mintWithTokenURI(accounts,tokenId,resdata['Image_url']).encodeABI(),
  };

     await window.ethereum
        .request({
            method: 'eth_sendTransaction',
            params: [tx],
        });
        fetch(`https://nft-app-ec882-default-rtdb.firebaseio.com/NFT/${name}.json`,
        {
          method:'PATCH',
          headers:{
            'CONTENT-TYPE': 'application/json',
          },
          body:JSON.stringify({
            'WalletAddress': accounts,
            'ContractAddress': cAddress,
            'Token': tokenId,
          })
        }
        );
        
        
  
  fetch(`https://nft-app-ec882-default-rtdb.firebaseio.com/token.json`,
  {
    method:'PATCH',
    headers:{
      'CONTENT-TYPE': 'application/json',
    },
    body:JSON.stringify({
      'count': tokenId,
    })
  }
  );
      }
 
    }
     catch (error){
       alert(error);
       if(error.message === 'MetaMask Tx Signature: User denied transaction signature.'){
        await fetch(`https://nft-app-ec882-default-rtdb.firebaseio.com/NFT/${name}.json`,{
          method:'DELETE'
        });
        alert('your data has been removed');
       }
 }
}
}

export default Contract;